import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ViewerDemoComponent } from "app/showcase/custom-components/viewer-demo/viewer-demo.component";
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { CneeSelectModule } from "app/custom-components/cnee-select/cnee-select.component";
import { ViewerDemoRoutingModule } from "app/showcase/custom-components/viewer-demo/viewer-demo.routing.module";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { ViewerDirective } from 'app/custom-components/viewer/viewer.directive';
import { ViewerDemoBasicComponent } from 'app/showcase/custom-components/viewer-demo/viewer-demo-basic.component';

@NgModule({
	declarations:[
		ViewerDirective,
		ViewerDemoBasicComponent,
		ViewerDemoComponent,

	],
	imports: [
		ViewerDemoRoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule
	]
})
export class ViewerDemoModule { }



